@font-face {
    font-family: valorantFont;
    src: url(../assets/VALORANT-Regular.woff);
}

body {
    background-color:rgb(21, 20, 32);
    position: relative;
}

header {
    position: relative;
    text-align: center;
    width: 750px;
    margin: auto;
    margin-top: 20px;
    padding-bottom: 20px;
    text-shadow: 2px 2px rgb(81, 71, 143);
    border-bottom: 1px solid rgb(81, 71, 143);
}

header h1 {
    font-size: 50px;
    font-family: valorantFont;
    color:white;
}

header h3 {
    font-size: 20px;
    font-family: fantasy, Copperplate;
    font-weight: 100;
    color: white;
}

.round-selector {
    margin-top: 25px;
    margin-bottom: 25px;
}

.round-selector {
    display: flex;
    justify-content: center;
}

.round-selector li {
    padding: 10px;
    width: 20px;
    text-align: center;
    border-right: 1px solid rgb(81, 71, 143);
    color: white;
    font-family: valorantFont;
    background-image: linear-gradient(to top, rgba(33, 57, 80, 0), rgb(20, 35, 48), rgba(33, 57, 80, 0));
}

.round-selector li:first-child {
    border-left: 1px solid rgb(81, 71, 143);
}

.round-selector li[data-selected=true] {
    background-color: rgb(81, 71, 143);
}

main {
    display: flex;
    justify-content: center;
    box-shadow: 2px 3px 5px black;
    width: 2000px;
    margin: auto;
    background-color: rgb(30, 28, 46);
}

.teams {
    width: 375px;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.team-1 {
    box-shadow: 1px 1px 10px black;
}

.team-2 {
    box-shadow: 1px 1px 10px black;
}

.teams ul ul.team-labels {
    padding-left: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: rgb(25, 39, 53);
}

.teams ul ul.team-labels > li {
    margin-top: 0px;
}

.teams img {
    width: 50px;
    padding: 0;
    box-shadow: 2px 2px 5px black;
}

.teams li:first-child {
    margin-right: 175px;
}

.teams > ul:first-child {
    margin-bottom: 40px;
}

.teams ul ul {
    display: flex;
    padding-left: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid rgb(81, 71, 143);
    border-left: 3px solid rgb(81, 71, 143);
    background-image: linear-gradient(to right, rgba(33, 57, 80, 1), rgba(33, 57, 80, 0), rgba(33, 57, 80, 0), );
    font-family: sans-serif;
    position: relative;
}

.teams ul ul[data-selected=true] {
    background-color: rgb(81, 71, 143);
}

.teams > ul > ul > li {
    vertical-align: middle;
    padding-left: 10px;
    margin-top: 17px;
}

.kda {
    position: absolute;
    right: 50px;
}

.stats {
    color: white;
    font-family:sans-serif;
    width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.stats section {
    border-bottom: 1px solid rgb(81, 71, 143);
}

.gun-list {
    text-align: center;
}

.gun-list li {
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid rgb(81, 71, 143);
    background-image: linear-gradient(to top, rgba(33, 57, 80, 1), rgba(33, 57, 80, 0), rgba(33, 57, 80, 0), rgba(33, 57, 80, 0));
}

.gun-list li:first-child {
    border-top: 1px solid rgb(81, 71, 143);
}

.gun-list img {
    width: 150px;
    margin: auto;
}

.gun-list li[data-selected=true] {
    background-image: linear-gradient(to right, rgba(81, 71, 143, 0), rgba(81, 71, 143, 0.5), rgba(81, 71, 143, 0));
}

.info {
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
}

footer {
    background-color: white;
    height: 60px;
    position: sticky;
    bottom: 0;
    width: 100%;
}
.dev-nav {
    display: flex;
    height: 30px;
    justify-content: center;
    vertical-align: middle;
    padding: 5px;
}

.map {
    display: flex;
    position: relative;
}

.map img {
    transform: rotate(90deg);
}

.marker {
    position: absolute;
    width: 20px;
    left: 415px;
}

a img {
    padding: 10px;
    vertical-align: middle;
}

.teams ul ul:hover {
    cursor: pointer;
    background-color:rgb(81, 71, 143);
}

.teams ul ul.team-labels:hover {
    cursor: default;
    background-color: rgb(25, 39, 53);
}

.round-selector li:hover {
    cursor: pointer;
    background-color: rgb(81, 71, 143)
}

.gun-list li:hover {
    cursor: pointer;
    background-color: rgb(81, 71, 143)
}

.map img {
    border: 1px solid pink;
}